import { logEvent } from "firebase/analytics";
import {
  addDoc,
  collection,
  getDocs,
  query,
  updateDoc,
  where,
} from "firebase/firestore";
import { useCallback, useEffect, useState } from "react";
import debounce from "lodash.debounce";
import ShortUniqueId from "short-unique-id";

import { analytics, firestore } from "./firebase";
import "./App.css";

const ID_LENGTH = 10;

const getId = () => {
  const urlSearchParams = new URLSearchParams(window.location.search);
  const params = Object.fromEntries(urlSearchParams.entries());

  if (params.id && params.id.length === ID_LENGTH) {
    return params.id;
  } else {
    const uid = new ShortUniqueId({ length: ID_LENGTH })();
    window.location.search = `?id=${uid}`;
    return uid;
  }
};

const getData = async (id) => {
  try {
    const dataRef = collection(firestore, "data");

    const q = query(dataRef, where("id", "==", id));
    const snapshot = await getDocs(q);

    let res;

    if (!snapshot.empty) {
      snapshot.forEach((doc) => {
        res = {
          ref: doc.ref,
          data: doc.data(),
        };
      });
    } else {
      res = {
        ref: await addDoc(dataRef, { id, data: "" }),
        data: { id, data: "" },
      };
    }
    return res;
  } catch (error) {
    alert(`Error: ${error.message}`);
  }
};

const storeData = debounce(async (docRef, id, data) => {
  try {
    logEvent(analytics, "store_data");
    await updateDoc(docRef, { id, data });

    console.log("Document written with ID: ", docRef.id);
  } catch (error) {
    alert(`Error: ${error.message}`);
  }
}, 300);

function App() {
  const [data, setData] = useState("");
  const [docRef, setDocRef] = useState();
  const [id, setId] = useState("");

  const getDataFirebase = useCallback(async () => {
    if (!id) {
      return;
    }

    const { ref, data } = await getData(id);

    setData(data.data);
    setDocRef(ref);
  }, [id]);

  const handleChange = useCallback(
    (e) => {
      const d = e.target.value;
      setData(d);
      storeData(docRef, id, d);
    },
    [docRef, id]
  );

  useEffect(() => {
    setId(getId());
  }, []);

  useEffect(() => {
    getDataFirebase();
  }, [getDataFirebase]);

  return (
    <div className="app">
      <p>Pastebin</p>
      <textarea value={data} onChange={handleChange} />
    </div>
  );
}

export default App;
